import { Observable, Subject } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

import {Post} from '../models/Post';


const httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization' : localStorage.getItem('token')
      // tslint:disable-next-line:max-line-length
      //'Authorization': 'Bearer eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpc3MiOiJodHRwOlwvXC8xMjcuMC4wLjciLCJpYXQiOjE1NTAzNDU5NjEsIm5iZiI6MTU1MDM0NTk2MSwiZXhwIjoxNTUwOTUwNzYxLCJkYXRhIjp7InVzZXIiOnsiaWQiOiIxIn19fQ.AJ08l_vE9cdpr3_ckxU2_9cbJXo-fznXAJrtvnuaCEI'
  })
  };

const token = localStorage.getItem('token');

@Injectable({
  providedIn: 'root',
})
export class PostService {

    wpUrl = localStorage.getItem('hostUrl') + '/wp-json/wp/v2/posts';
    token = localStorage.getItem('token');
    username = localStorage.getItem('username');
    password = localStorage.getItem('password');



    // posts$: Observable<Post[]>;
    posts: any;

    obj: any;
    items = [];

    postId = '';
    post: Post;

   // load = false;
    todos = true;

    /* Load the spinner state */
    success = false;
    postsTotal = 0;


  constructor(private http: HttpClient) {}


  getPosts() {
    this.items = [];
    this.post = {
        id: 0,
        title: '',
        content: '',
        status: '',
        link: '',
        date: ''
    };


     this.http.get<Post[]>(this.wpUrl).pipe().toPromise()
    .then( post =>
        {
            this.posts = post;
            this.postsTotal = post.length;
        })
    .catch ((e) => console.log('Something wrong happeend'))
    .finally (() => {

      for (let i = 0; i < this.posts.length; i++) {
          const obj = {
            id: 0,
            title: '',
            content: '',
            status: '',
            link: '',
            date: '',
          };
          obj.id = this.posts[i].id;
          obj.title = this.posts[i].title['rendered'];
          obj.content = this.posts[i].content['rendered'];
          obj.status = this.posts[i].status;
          obj.link = this.posts[i].link;
          obj.date = this.posts[i].date;

          this.items.push(obj);
      }
      this.success = true;

    });

    return this.items;
  }

  getThreePosts() {
    this.items = [];
    this.post = {
        id: 0,
        title: '',
        content: '',
        status: '',
        link: '',
        date: ''
    };


     this.http.get<Post[]>(this.wpUrl).pipe().toPromise()
    .then( post => {
            this.posts = post;
            this.postsTotal = post.length;

        })
    .catch ((e) => console.log('Something wrong happeend'))
    .finally (() => {

      for (let i = 0; i < 3; i++) {
          const obj = {
            id: 0,
            title: '',
            content: '',
            status: '',
            link: '',
            date: '',
          };
          obj.id = this.posts[i].id;
          obj.title = this.posts[i].title['rendered'];
          obj.content = this.posts[i].content['rendered'];
          obj.status = this.posts[i].status;
          obj.link = this.posts[i].link;
          obj.date = this.posts[i].date;

          this.items.push(obj);
      }
      this.success = true;

    });

    return this.items;
  }


  GetPostById(id) {

   // this.todos = false;

    const url = `${this.wpUrl}/${id}`;

    this.http.get<Post>(url).pipe().toPromise().
     then( p => {
         this.post.id = p.id;
         this.post.title = p.title['rendered'];
         this.post.content = p.content['rendered'];
         this.post.status = p.status;
         this.post.date = p.date;
     })
     .catch ((e) => 'Something wrong happened')
     .finally( () => {
         //  this.load = true;
           this.success = true;
     });


     return this.post;
}



postUpdate(id, post: Post) {

   // this.load = false;
    const url = `${this.wpUrl}/${id}`;

    this.auth();
    this.http.post<Post>(url, post, httpOptions).pipe().toPromise().
    then( p => {})
    .catch ((e) => 'Something wrong happened')
    .finally( () => {
          this.success = true;
         // this.getPosts();
    });
}


AddPost(title, content) {

  const newPost = {
     title: title,
     content: content,
     status : 'publish'
  };
  this.auth();
  this.http.post<Post>(this.wpUrl, newPost, httpOptions).pipe().subscribe();
 // this.getPosts();

 }

 deletePost(id) {
  this.auth();
  const url = `${this.wpUrl}/${id}`;
  this.http.delete<Post>(url, httpOptions).pipe().subscribe();
 // this.getPosts();

}

auth() {

  const apiHost = 'http://127.0.0.7/wp-json';

  this.http.post( apiHost + '/jwt-auth/v1/token', {
      username: this.username,
      password: this.password
    }).subscribe(p => {
       // console.log('Estas son mis credenciales', p);

    });
}

}
