import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Location} from '@angular/common';
import { FormGroup, FormControl } from '@angular/forms';

import { PostService} from '../../shared/services/post.service';
import { Post} from '../../shared/models/Post';

@Component({
  selector: 'app-postedit',
  templateUrl: './post-edit.component.html',
  styleUrls: ['./post-edit.component.css']
})
export class PosteditComponent implements OnInit {

    post: Post;
    id = '';

    success = false;

    postForm = new FormGroup({
        title: new FormControl(''),
        content: new FormControl(''),
      });

  constructor( public postService: PostService,
    private route: ActivatedRoute,
    private router: Router,
    private location: Location
    ) {
        this.post = {
            title: '',
            content: '',
            status: '',
            link: '',
            id: 0,
            date: ''
        };
     }

  ngOnInit() {

    this.id = this.route.snapshot.params['id'];
    this.post = this.postService.GetPostById(this.id);
    console.log(this.post);

        this.postForm = new FormGroup ({
            title: new FormControl(this.post.title),
            content:  new FormControl(this.post.content)
     });





  }

  onGoBack(){
      this.postService.success = false;
      this.location.back();
  }

  onSubmit(){
      this.post.title = this.postForm.value['title'];
      this.post.content = this.postForm.value['content'];
      this.success = true;
      this.postService.postUpdate(this.id, this.post);
     // this.router.navigate(['/']);
  }

}
