import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, Subject } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

import {Comment} from '../models/Comment';
import { Content } from '@angular/compiler/src/render3/r3_ast';


const httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization' : localStorage.getItem('token')
      // tslint:disable-next-line:max-line-length
      //'Authorization': 'Bearer eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpc3MiOiJodHRwOlwvXC8xMjcuMC4wLjciLCJpYXQiOjE1NTAzNDU5NjEsIm5iZiI6MTU1MDM0NTk2MSwiZXhwIjoxNTUwOTUwNzYxLCJkYXRhIjp7InVzZXIiOnsiaWQiOiIxIn19fQ.AJ08l_vE9cdpr3_ckxU2_9cbJXo-fznXAJrtvnuaCEI'
  })
  };

const token = localStorage.getItem('token');

@Injectable({
  providedIn: 'root',
})
export class CommentService {

    wpUrl = localStorage.getItem('hostUrl') + '/wp-json/wp/v2/comments';
    token = localStorage.getItem('token');
    username = localStorage.getItem('username');
    password = localStorage.getItem('password');




    // posts$: Observable<Post[]>;
    comments: any;

    obj: any;
    items = [];

    commentId = '';
    comment: Comment;


    /* Load the spinner state */
    success = false;
    commentsTotal = 0;


  constructor(private http: HttpClient) {}



  getThreeComments() {
    this.items = [];
    this.comment = {
        id: 0,
        content: '',
        status: '',
        author_name: '',
        author_avatar_urls: '',
        date: ''
    };


     this.http.get<Comment[]>(this.wpUrl).pipe().toPromise()
    .then( comment => {

            this.comments = comment;
            this.commentsTotal = comment.length;


        })
    .catch ((e) => console.log('Something wrong happeend'))
    .finally (() => {

      if(this.comments.length < 1 ) {
        return;
      }

      for (let i = 0; i < 3; i++) {
          const obj = {
            id: 0,
            content: '',
            status: '',
            author_name: '',
            author_avatar_urls: '',
            date: ''
          };

          obj.id = this.comments[i].id;
          obj.content = this.comments[i].content['rendered'];
          obj.status = this.comments[i].status;
          obj.author_name = this.comments[i].author_name;
          obj.author_avatar_urls = this.comments[i].author_avatar_urls[48];

          this.items.push(obj);
      }
      this.success = true;

    });

    return this.items;
  }


  getComments() {
    this.items = [];
    this.comment = {
        id: 0,
        content: '',
        status: '',
        author_name: '',
        author_avatar_urls: '',
        date: ''
    };


     this.http.get<Comment[]>(this.wpUrl).pipe().toPromise()
    .then( comment => {

            this.comments = comment;
            this.commentsTotal = comment.length;


        })
    .catch ((e) => console.log('Something wrong happeend'))
    .finally (() => {

      for (let i = 0; i < this.comments.length; i++) {
          const obj = {
            id: 0,
            content: '',
            status: '',
            author_name: '',
            author_avatar_urls: '',
            date: ''
          };
          obj.id = this.comments[i].id;
          obj.content = this.comments[i].content['rendered'];
          obj.status = this.comments[i].status;
          obj.author_name = this.comments[i].author_name;
          obj.author_avatar_urls = this.comments[i].author_avatar_urls[48];

          this.items.push(obj);
      }
      this.success = true;

    });

    return this.items;
  }



 deleteComment(id) {
  this.auth();
  const url = `${this.wpUrl}/${id}`;
  this.http.delete<Comment>(url, httpOptions).pipe().subscribe();
  //this.comments = this.getComments();

}



  GetCommentById(id) {

   // this.todos = false;

    const url = `${this.wpUrl}/${id}`;

    this.http.get<Comment>(url).pipe().toPromise().
     then( p => {
       this.comment.id = p.id;
       this.comment.author_name = p.author_name;
       this.comment.author_avatar_urls = p.author_avatar_urls[48];
       this.comment.content = p.content['rendered'];
       this.comment.status = p.status;
       this.comment.date = p.date;
     })
     .catch ((e) => 'Something wrong happened')
     .finally( () => {

           this.success = true;
     });


     return this.comment;
}

auth() {

  const apiHost = 'http://127.0.0.7/wp-json';

  this.http.post( apiHost + '/jwt-auth/v1/token', {
    username: this.username,
    password: this.password
    }).subscribe();
}

}
