import { Component, OnInit, AfterViewInit } from '@angular/core';
import { Router} from '@angular/router';
import { Chart } from 'chart.js';
import {PostService} from '../../shared/services/post.service';
import { CommentService } from '../../shared/services/comment.service';
import { UserService } from '../../shared/services/user.service';
// import { Comment } from './../../shared/models/Comment';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit, AfterViewInit {
  chartOptions = {
    responsive: true
  };

  chartData = [
    { data: [this.postService.postsTotal], label: 'Posts' },
    { data: [], label: 'Comments' },
    { data: [1], label: 'Users' }
  ];

  chartLabels = ['January', 'February', 'Mars', 'April'];

  items = [];
  comments = [];



  constructor(
    private postService: PostService,
    private commentService: CommentService,
    private userService: UserService,
    private router: Router) {}

  ngOnInit() {

    this.items = [];
    this.comments = [];





    if (localStorage.getItem('hostUrl') === null) {
        this.router.navigate(['/settings']);
    }


      this.items = this.postService.getThreePosts();

      this.comments = this.commentService.getThreeComments();

      this.userService.getUsers();


  }

  onClickCommentDetail(id){
      console.log('Clicked...', id);
      this.commentService.success = false;
      this.router.navigate(['/comment/' + id]);
  }

  onClickPostDetail(id){
    console.log('Clicked...', id);
    this.postService.success = false;
    this.router.navigate(['/postdetail/' + id]);
}


  onChartClick(event) {
    console.log(event);

    this.chartData = [
        { data: [this.postService.postsTotal], label: 'Posts' },
        { data: [this.commentService.commentsTotal], label: 'Comments' },
        { data: [1], label: 'Users' }
     ];
  }

  ngAfterViewInit(): void {

  }

}
