import { Component, OnInit } from '@angular/core';
import { Router} from '@angular/router';
import { FormGroup, FormControl } from '@angular/forms';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit {

  settingForm = new FormGroup({
        hostUrl: new FormControl(''),
        token: new FormControl(''),
        username: new FormControl(''),
        password: new FormControl(''),
      });
  constructor( private router: Router) { }

  ngOnInit() {

      if (localStorage.getItem('hostUrl') !== null) {
         this.settingForm = new FormGroup ({
             hostUrl: new FormControl(localStorage.getItem('hostUrl').toString()),
             token:  new FormControl(localStorage.getItem('token').toString()),
             username:  new FormControl(localStorage.getItem('username').toString()),
             password:  new FormControl(localStorage.getItem('password').toString()),
      });
    }
  }

  onSubmit() {
    // TODO: Use EventEmitter with form value
    localStorage.setItem('hostUrl', this.settingForm.value['hostUrl']);
    localStorage.setItem('token', this.settingForm.value['token']);
    localStorage.setItem('username', this.settingForm.value['username']);
    localStorage.setItem('password', this.settingForm.value['password']);
    this.router.navigate(['/']);
  }

}
