const token = localStorage.getItem('token');

@Injectable({
  providedIn: 'root',
})
export class UserService {

    wpUrl = localStorage.getItem('hostUrl') + '/wp-json/wp/v2/users';
    token = localStorage.getItem('token');
    username = localStorage.getItem('username');
    password = localStorage.getItem('password');




    // posts$: Observable<Post[]>;
    users: any;

    obj: any;
    items = [];

    userId = '';
    user: User;



    /* Load the spinner state */
    success = false;
    usersTotal = 0;


  constructor(private http: HttpClient) {}


  getUsers() {
    this.items = [];
    this.user = {
        id: 0,
        name: '',
        avatar_urls: '',
        link: ''
    };


     this.http.get<User[]>(this.wpUrl).pipe().toPromise()
    .then( user =>
        {
            this.users = user;
            this.usersTotal = user.length;
        })
    .catch ((e) => console.log('Something wrong happeend'))
    .finally (() => {

      for (let i = 0; i < this.users.length; i++) {
          const obj = {
            id: 0,
            name: '',
            avatar_urls: '',
            link: ''
          };
          obj.id = this.users[i].id;

          obj.name = this.users[i].name;
          obj.avatar_urls = this.users[i].avatar_urls[48];
          obj.link = this.users[i].link;

          this.items.push(obj);
      }
      this.success = true;

    });

    return this.items;
  }




auth() {

  const apiHost = 'http://127.0.0.7/wp-json';

  this.http.post( apiHost + '/jwt-auth/v1/token', {
    username: this.username,
    password: this.password
    }).subscribe(p => {
       // console.log('Estas son mis credenciales', p);

    });
}

}
